@import '../../styles/variables';
@import '../../styles/mixins';


.heroSection{
    @include flex-container(column,flex-start,center,0);   
    .heroTopBar{
        @include flex-container(row,center, space-between, 0);
        width: 100%;
        margin-bottom: 2.37rem;
        .getStartedBtn{
            @include button-custom($dark-blue, $white, .56rem,1.5rem, 1.75rem);
        }
    
        img{
            max-width: 5.56rem;
            width: 100%;
        }
        @include for-tablet{
            margin-bottom: 6.18rem;
            
        }
    }
    .heroDescription{
        h1{
            font-size: $h-size-M;
            color:$dark-blue;
            font-weight: $extra-bold;
            margin-bottom: 1.65rem;
        }
        p{
            margin-bottom: 1.5rem;
        }
        .getStartedBtn{
            @include button-custom-linear($linear-gradient-2,$linear-gradient-1, $white,1rem, 2.37rem,1.75rem);
            margin-bottom: 2.85rem;
        }

        @include for-tablet{
            max-width: 400px;
            h1{
                max-width: 330px;
            }
        }
    }

    .heroSectionContainer{
        display: flex;
        flex-direction: column;
        .heroImgContainer{
            width: 100%;
            height: 300px;
            background-image: url('../../../public/images/image-hero-mobile@2x.webp');
            background-size: 400px;
            background-repeat: no-repeat;
            background-position: center;
        }
        
        
  
        @include for-tablet{
            flex-direction: row;

            .heroImgContainer{
                display: none;
            } 
        }

        @include for-desktop{
            
        }
    }

    
    
    

}

