@import './styles/resets';
@import './styles/variables';
@import './styles/mixins';

body {
  min-height: 100vh;
  font-family: $Jakarta;
  color:$dark-grey;
  background-color: $white;
  overflow-x: hidden;
  @include for-tablet{
    
    background-image: url('../public/images/image-hero-tablet@2x.webp');
    background-size: 640px;
    background-repeat: no-repeat;
    background-position: top -50px right -270px;
  }
}


